import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SalesOrderCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/sales/salesOrder/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/sales/sales-orders";

class CreateSalesOrder extends Component {
  componentDidMount() {
    const { createSalesOrder } = this.props;
    createSalesOrder();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesOrder.error) {
      notification("error", this.props.salesOrder.error);
      this.props.clearError();
    }
    if (this.props.salesOrder.success && this.props.salesOrder.id_sales_order) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.salesOrder.id_sales_order
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.salesOrder.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storeSalesOrder } = this.props;
    storeSalesOrder(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            // marginBottom={true}
            title={<IntlMessages id="salesOrders.button.add" />}
          >
            <Button
              onClick={() => {
                removeSaveData("so");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <SalesOrderCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            salesOrder={this.props.salesOrder}
            history={this.props.history}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesOrder: state.salesOrder,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CreateSalesOrder);
