const actions = {
  getSalesOrders: (page, search, filter) => ({
    type: "GET_SALES_ORDERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createSalesOrder: () => ({
    type: "CREATE_SALES_ORDER_REQUEST"
  }),
  getItemsForSO: (
    id_customer,
    id_location,
    date,
    id_payment_category,
    search
  ) => ({
      type: "GET_FILTER_ITEMS_FOR_SO_REQUEST",
    payload: {
      id_customer: id_customer,
      id_location: id_location,
      id_payment_category,
      date: date,
      search: search
    }
  }),
  getItemsByLocation: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_BY_LOCATION_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_SALES_ORDERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getCustomerContacts: id_customer => ({
    type: "GET_CUTOMER_CONTACTS_FOR_SO_REQUEST",
    payload: { id_customer: id_customer }
  }),
  getItemRequests: (page, search, filter) => ({
    type: "GET_FILTER_ITEM_REQUESTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeSalesOrder: data => ({
    type: "STORE_SALES_ORDER_REQUEST",
    payload: { body: data }
  }),
  editSalesOrder: (id_sales_order, search) => ({
    type: "EDIT_SALES_ORDER_REQUEST",
    payload: { id_sales_order: id_sales_order, search: search }
  }),
  updateSalesOrder: (id_sales_order, data) => ({
    type: "UPDATE_SALES_ORDER_REQUEST",
    payload: {
      id_sales_order: id_sales_order,
      body: data
    }
  }),
  showSalesOrder: id_sales_order => ({
    type: "SHOW_SALES_ORDER_REQUEST",
    payload: { id_sales_order: id_sales_order }
  }),
  confirmSalesOrder: (id_sales_order, data) => ({
    type: "CONFIRM_SALES_ORDER_REQUEST",
    payload: { id_sales_order, body: data }
  }),
  unconfirmSalesOrder: (id_sales_order, data) => ({
    type: "UNCONFIRM_SALES_ORDER_REQUEST",
    payload: { id_sales_order, body: data }
  }),
  closeSalesOrder: id_sales_order => ({
    type: "CLOSE_SALES_ORDER_REQUEST",
    payload: { id_sales_order }
  }),
  cancelSalesOrder: (id_sales_order, data) => ({
    type: "CANCEL_SALES_ORDER_REQUEST",
    payload: { id_sales_order, body: data }
  }),
  // attachment management
  uploadDocumentSO: (id_sales_order, data) => ({
    type: "UPLOAD_SALES_ORDER_DOCUMENT_REQUEST",
    payload: {
      id_sales_order,
      body: data
    }
  }),
  downloadDocumentSO: id_sales_order_document => ({
    type: "DOWNLOAD_SALES_ORDER_DOCUMENT_REQUEST",
    payload: { id_sales_order_document }
  }),
  deleteDocumentSO: (id_sales_order_document, id_sales_order) => ({
    type: "DELETE_SALES_ORDER_DOCUMENT_REQUEST",
    payload: {
      id_sales_order_document: id_sales_order_document,
      id_sales_order: id_sales_order
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  getUpdateSODetails: data => ({
    type: "GET_UPDATE_SALES_ORDER_DETAILS_REQUEST",
    payload: {
      body: data
    }
  })
};

export default actions;
