import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import ModalItemDetail from "./modalItemDetail";
import {
  // getBaseQty,
  getDiscountBillPerItem,
  // getItemPrice,
  getNettPurchPrice,
  getSalesPriceAndDiscountNonDM,
  getSubTotalPerItem,
  getSubTotalTaxPerItem,
  getSubTotalTaxPerItemInclusive,
  getUnitConversion,
  getItemDiscountDM
} from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      bill_discount: 0,
      count: items.length + 1,
      dataSource: [...items],
      discount_amount: 0,
      discount_pct: 0,
      limit: 0,
      selectedItem: {},
      taxes: [],
      totalQty: 0,
      total_discount: 0,
      total_exclude_discount_tax: 0,
      sum_subtotal_discount_item: 0,
      total_tax: 0,
      visible: false
    };
  }

  componentDidUpdate(prevProps) {
    // console.log("list items ", this.state.dataSource);
    const {
      clear,
      clearItem,
      clearItems,
      handleSaveItemList,
      inclusive_tax,
      item,
      items,
      loadingItems,
      loadingTransactions,
      custDisc,
      discountItems,
      // taxes
    } = this.props;

    // milih item sendiri
    if (!loadingTransactions && !loadingItems && item) {
      const { dataSource, count, totalQty, sum_subtotal_discount_item } = this.state;
      // console.log("item yang dipilih", item);
      const find = dataSource.findIndex(
        currData =>
          !currData.id_item_request && currData.id_item === item.id_item
      );
      // console.log(find);

      // item baru
      if (find < 0) {
        const disc = item.discount ? item.discount : "0%";
        const price = item.price ? parseFloat(item.price) : 0;

        let init = {
          preference_item: {
            sale_tax: item.preference_item.sale_tax,
            sale_unit: item.preference_item.sale_unit
          },
          customer_pricelists: item.customer_pricelists,

          index_no: count,
          id_sales_order: null,
          id_sales_order_detail: null,

          id_item: item.id_item,
          id_item_category: item.id_item_category,
          item_code: item.item_code,
          sku: item.sku,
          item_name: item.item_name,
          item_type: item.item_type,

          qty: 1,
          item_price: price,
          location_price: item.location_price
            ? parseFloat(item.location_price)
            : 0,

          id_unit: item.preference_item.sale_unit.id_unit,
          unit_code: item.preference_item.sale_unit.unit_code,
          unit_conversion: item.unit_conversion,
          unit_conv: 1,

          id_base_unit: item.base_unit.id_unit,
          base_unit_code: item.base_unit.unit_code,
          base_qty: 1,

          // item_discount = menyimpan initial discount dari item
          item_discount: item.discount ? item.discount : "0%",
          id_discount: null,
          discount_code: "-",
          discount: disc,
          bill_discount: 0,
          // rounding: 0,

          id_tax: item.preference_item.sale_tax.id_tax,
          tax_pct: item.preference_item.sale_tax.percentage,
          tax_name: item.preference_item.sale_tax.tax_name,

          id_project: null,
          id_department: null,
          item_note: null,
        };

        init.unit_conv = getUnitConversion(init.id_unit, init.unit_conversion);
        init.base_qty = init.qty * init.unit_conv;
        init = getItemDiscountDM(init, discountItems);

        // generate sub_total
        const subtotal = getSubTotalPerItem(price, 1, init.discount);

        // generate sub_total_tax
        let sub_total_tax;
        if (inclusive_tax) {
          sub_total_tax = getSubTotalTaxPerItemInclusive(
            Object.assign({}, init),
            [...dataSource, init],
            custDisc,
            inclusive_tax
          );
        } else {
          sub_total_tax = getSubTotalTaxPerItem(
            Object.assign({}, init),
            [...dataSource, init],
            custDisc,
            inclusive_tax
          );
        }

        const newData = Object.assign({}, init, {
          netto_sales_price: getNettPurchPrice(
            Object.assign({}, init),
            [...dataSource, init],
            inclusive_tax,
            custDisc
          ),
          bill_discount: getDiscountBillPerItem(
            Object.assign({}, init),
            [...dataSource, init],
            custDisc
          ),
          sub_total: subtotal,
          sub_total_tax: sub_total_tax
        });
        // console.log("dari editable ", inclusive_tax, custDisc);
        const new_sum_subtotal_discount_item =
          sum_subtotal_discount_item +
          getSubTotalPerItem(price, 1, init.discount);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalQty: totalQty + 1,
          sum_subtotal_discount_item: new_sum_subtotal_discount_item
        });

        clearItem([...dataSource, newData], totalQty + 1, new_sum_subtotal_discount_item);
        // console.log(this.props.item);
      }
      // penambahan 1 quantity jika item yang dipilih sudah ada
      else if (find >= 0) {
        let newTotQty = 0;
        const updateData = dataSource;
        let newData = Object.assign({}, updateData[find]);

        newData.qty = parseFloat(newData.qty) + 1;

        // --------------- discount management
        newData = getItemDiscountDM(newData, discountItems);
        // --------------- /discount management

        newData.base_qty = parseFloat(newData.qty) * newData.unit_conv;
        // newData.item_price = getItemPrice(newData);
        // newData.unit_conv = getUnitConversion(
        //   newData.id_unit,
        //   newData.unit_conversion
        // );

        newData.sub_total = getSubTotalPerItem(
          newData.item_price,
          newData.qty,
          newData.discount
        );
        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });
        newData.netto_sales_price = getNettPurchPrice(
          newData,
          [...updateData],
          inclusive_tax,
          custDisc
        );
        newData.bill_discount = getDiscountBillPerItem(
          newData,
          [...updateData],
          custDisc
        );

        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });

        let new_sum_subtotal_discount_item = 0;
        updateData.map(currData => {
          newTotQty += parseFloat(currData.base_qty);
          new_sum_subtotal_discount_item += getSubTotalPerItem(
            currData.item_price,
            currData.qty,
            currData.discount
          );
        });

        this.setState({
          dataSource: [...updateData],
          totalQty: newTotQty,
          sum_subtotal_discount_item: new_sum_subtotal_discount_item
        });
        clearItem(updateData, newTotQty, new_sum_subtotal_discount_item);
        // console.log(this.props.item);
      }
    }
    // jika item dari transaksi sebelumnya
    else if (!loadingTransactions && !loadingItems && items.length !== 0) {
      let newData = [];
      let newTotQty = 0;
      let newIndex = 0;
      let new_sum_subtotal_discount_item = 0;

      items.map(item => {
        // let price = 0;
        let disc = 0;
        let init = {};

        // init.preference_item = {
        //   sale_tax: item.preference_item 
        //     ? item.preference_item.sale_tax
        //     : item.item.preference_item.sale_tax,
        //   sale_unit: item.preference_item 
        //     ? item.preference_item.sale_unit 
        //     : item.item.preference_item.sale_unit
        // };
        init.customer_pricelists = item.customer_pricelists
          ? item.customer_pricelists
          : item.item
            ? item.item.customer_pricelists
              ? item.item.customer_pricelists
              : []
            : [];

        init.index_no = item.index_no;
        init.id_sales_order = item.id_sales_order ? item.id_sales_order : null;
        init.id_sales_order_detail = item.id_sales_order_detail ? item.id_sales_order_detail : null;

        init.id_item = item.id_item;
        init.id_item_category = item.item
          ? item.item.id_item_category
          : item.id_item_category;
        init.item_code = item.item_code;
        init.sku = item.item
          ? item.item.sku
          : item.sku;
        init.item_name = item.item_name;
        init.item_type = item.item
          ? item.item.item_type
          : item.item_type;

        init.qty = item.qty ? parseFloat(item.qty) : parseFloat(item.request_qty);
        init.item_price = item.item_price
          ? item.item_price
          : item.item_price === 0
            ? 0
            : item.last_sale_price;
        init.location_price = item.location_price
          ? parseFloat(item.location_price)
          : parseFloat(item.cost_per_unit);

        init.id_unit = item.id_unit;
        init.unit_code = item.unit
          ? item.unit.unit_code
          : item.unit_code;
        init.unit_conversion = item.unit_conversion
          ? item.unit_conversion
          : item.item.unit_conversion;
        init.unit_conv = getUnitConversion(init.id_unit, init.unit_conversion);

        init.id_base_unit = item.id_base_unit
          ? item.id_base_unit
          : item.id_unit;
        init.base_unit_code = item.base_unit
          ? item.base_unit.unit_code
          : item.base_unit_code;
        init.base_qty = init.qty * init.unit_conv;

        item.qty = item.qty ? item.qty : item.request_qty;
        item.base_qty = item.base_qty
          ? item.base_qty
          : parseFloat(item.qty);
        item.unit_code = item.unit
          ? item.unit.unit_code
          : item.unit_code;
        item.id_base_unit = item.id_base_unit
          ? item.id_base_unit
          : item.id_unit;
        item.base_unit_code = item.base_unit
          ? item.base_unit.unit_code
          : item.base_unit_code;

        init.id_discount = item.id_discount;
        init.discount = item.discount;

        // if items are came from loc/cust/payment method changed
        if (this.props.calcDiscItem) {
          // console.log("item dari pergantian lokasi/customer/payment");
          // item_discount and item_price is calculated by backend server
          if (init.discount) {
            init.item_discount = init.discount;
          } else {
            disc = parseFloat(item.discount) * 100 + "%";
            init.item_discount = disc;
            init.discount = disc;
          }

          init = getItemDiscountDM(init, discountItems);
        }
        // if items are came from prev transaction (updateForm cases)
        else {
          // console.log("item dari transaksi sebelumnya");
          const price_list = getSalesPriceAndDiscountNonDM(init);
          init.item_discount = price_list.discount;
          // console.log(
          //   "hasil dari inital discount",
          //   item.item_discount,
          //   "dari",
          //   item.customer_pricelists
          // );
          if (init.id_discount) {
            init.discount_code = item.discount_management.discount_code;
          } else {
            init.discount_code = null;
            init.id_discount = null;
          }
          if (!init.discount) {
            const disc = parseFloat(init.discount) + "%";
            init.discount = disc;
          }
        }

        init.id_tax = item.id_tax
          ? item.id_tax
          : item.tax
            ? item.tax.id_tax
            : item.preference_item.sale_tax.id_tax;
        init.tax_pct =
          item.tax_pct || item.tax_pct === 0
            ? item.tax_pct
            : item.tax
              ? parseFloat(item.tax.percentage)
              : parseFloat(item.preference_item.sale_tax.percentage);
        init.tax_name = item.tax
          ? item.tax.tax_name
          : item.tax_name
            ? item.tax_name
            : item.preference_item.sale_tax.tax_name;

        init.id_project = item.id_project ? item.id_project : null;
        init.id_department = item.id_department ? item.id_department : null;
        init.item_note = item.item_note ? item.item_note : null;

        init.sub_total = getSubTotalPerItem(
          init.item_price,
          init.qty,
          init.discount
        );

        if (this.props.exceededItems && this.props.exceededItems.length > 0) {
          const { exceededItems } = this.props;
          const getExceeded = exceededItems.find(
            data => data.id_item === init.id_item
          );
          if (getExceeded) {
            init.current_qty = getExceeded.current_qty;
          }
        }

        init.netto_sales_price = null;

        newTotQty += parseFloat(init.base_qty);
        newIndex = parseInt(init.index_no) + 1;
        new_sum_subtotal_discount_item += getSubTotalPerItem(
          init.item_price,
          init.qty,
          init.discount
        );
        newData.push(init);
      });

      newData.map(currData => {
        // generate sub_total_tax
        let sub_total_tax;
        if (inclusive_tax) {
          sub_total_tax = getSubTotalTaxPerItemInclusive(
            currData,
            [...newData],
            custDisc,
            inclusive_tax
          );
        } else {
          sub_total_tax = getSubTotalTaxPerItem(
            currData,
            [...newData],
            custDisc,
            inclusive_tax
          );
        }
        currData.sub_total_tax = sub_total_tax;
        currData.sub_total = getSubTotalPerItem(
          currData.item_price,
          currData.qty,
          currData.discount
        );

        currData.netto_sales_price = getNettPurchPrice(
          currData,
          [...newData],
          inclusive_tax,
          custDisc
        );
        currData.bill_discount = getDiscountBillPerItem(
          currData,
          [...newData],
          custDisc
        );
      });

      // console.log(newData);
      this.setState({
        dataSource: [...newData],
        totalQty: newTotQty,
        count: newIndex,
        sum_subtotal_discount_item: new_sum_subtotal_discount_item
      });
      clearItems(newData, newTotQty, new_sum_subtotal_discount_item);
    }
    // Jika inclusive tax
    else if (prevProps.inclusive_tax !== inclusive_tax && items.length === 0) {
      const { sum_subtotal_discount_item } = this.state;
      let newData = this.state.dataSource;
      // const sumSubtotal = getSumSubtotal(newData);

      newData.map(data => {
        data.netto_sales_price = getNettPurchPrice(
          data,
          newData,
          inclusive_tax,
          custDisc
        );
        // generate sub_total_tax
        let sub_total_tax;
        if (inclusive_tax) {
          sub_total_tax = getSubTotalTaxPerItemInclusive(
            data,
            newData,
            custDisc,
            inclusive_tax
          );
        } else {
          sub_total_tax = getSubTotalTaxPerItem(
            data,
            newData,
            custDisc,
            inclusive_tax
          );
        }
        data.sub_total_tax = sub_total_tax;
        data.bill_discount = getDiscountBillPerItem(data, newData, custDisc);
      });
      this.setState({ dataSource: [...newData] });
      handleSaveItemList(newData, this.state.totalQty, sum_subtotal_discount_item, undefined, false);
    } else if (prevProps.custDisc !== custDisc && items.length === 0) {
      // console.log(prevProps.custDisc, custDisc)
      const { sum_subtotal_discount_item } = this.state;
      let newData = this.state.dataSource;
      // const sumSubtotal = getSumSubtotal(newData);

      newData.map(data => {
        data.netto_sales_price = getNettPurchPrice(
          data,
          newData,
          inclusive_tax,
          custDisc
        );
        data.bill_discount = getDiscountBillPerItem(data, newData, custDisc);
      });
      this.setState({ dataSource: [...newData] });
      handleSaveItemList(newData, this.state.totalQty, sum_subtotal_discount_item, undefined, false);
    }
    // clear item jika location/supplier berubah
    else if (clear) {
      this.setState({ dataSource: [], count: 1, sum_subtotal_discount_item: 0 });
      handleSaveItemList([], 0, 0, undefined, true);
    }
  }

  // calculating discount per item based on id_discount in item
  calcDiscItem = (idDisc, item) => {
    // console.log("calc disc per item");
    const { discountItems } = this.props;

    const findDiscManag = discountItems.find(
      data => data.id_discount_management === idDisc
    );

    var disc;

    // type 1 - event
    if (findDiscManag.discount_type === 1) {
      disc = findDiscManag.discount_management_details[0].discount_value;
    }
    // type 2 - quantity
    else if (findDiscManag.discount_type === 2) {
      const discManagDetail = findDiscManag.discount_management_details;
      // filtering discount with the same id_unit
      var discManagDetailSameUnits = discManagDetail.filter(
        data => data.id_unit === item.id_unit
      );

      // filtering discount with all unit
      var discManagDetailAllUnits = discManagDetail.filter(
        data => !data.id_unit
      );

      // local function to find which discount management detail will be used
      function searchDisc(data, index, arr) {
        if (arr[index + 1]) {
          return (
            item.qty < arr[index + 1].min_qty_amount &&
            item.qty >= data.min_qty_amount
          );
        } else {
          return item.qty >= data.min_qty_amount;
        }
      }

      // create temp var for saving index of the found discount
      var indexFindDiscManagDetailSame, indexFindDiscManagDetailAll;

      // finding discount management detail for the same unit
      if (discManagDetailSameUnits.length > 0) {
        indexFindDiscManagDetailSame = discManagDetailSameUnits.findIndex(
          (data, index) => searchDisc(data, index, discManagDetailSameUnits)
        );
      }

      // finding discount management detail for all unit
      if (discManagDetailAllUnits.length > 0) {
        indexFindDiscManagDetailAll = discManagDetailAllUnits.findIndex(
          (data, index) => searchDisc(data, index, discManagDetailAllUnits)
        );
      }

      // deciding which discount management detail to use
      if (indexFindDiscManagDetailSame >= 0) {
        disc =
          discManagDetailSameUnits[indexFindDiscManagDetailSame].discount_value;
      } else if (indexFindDiscManagDetailAll >= 0) {
        disc =
          discManagDetailAllUnits[indexFindDiscManagDetailAll].discount_value;
      }

      // if (!disc) {
      //   disc = item.item_discount ? item.item_discount : "0%";
      // }
    }
    return disc;
  };

  // mapping id_discount per item
  mapDiscPerItem = (item, fromPrevTrans = false) => {
    var validDiscList = []; //initiate variabel untuk menyimpan daftar diskon yang valid untuk item yang bersangkutan
    var itemValidDisc = {}; //objek yang akan disimpan ke dalam validDiscList

    const { discountItems } = this.props;
    // --------------- discount management
    var disc = 0; // disc untuk menyimpan initial discount yang ada pada item

    // if discountItems exist
    if (discountItems.length > 0) {
      discountItems.map(data => {
        // untuk item_plu
        if (item.item_code === data.item_plu) {
          itemValidDisc = {
            id_discount_management: data.id_discount_management,
            discount_code: data.discount_code,
            priority: 1
          };
          validDiscList.push(itemValidDisc);
        }
        // untuk item_sku
        else if (item.sku === data.item_sku) {
          itemValidDisc = {
            id_discount_management: data.id_discount_management,
            discount_code: data.discount_code,
            priority: 2
          };
          validDiscList.push(itemValidDisc);
        } else if (item.item && item.item.sku === data.item_sku) {
          itemValidDisc = {
            id_discount_management: data.id_discount_management,
            discount_code: data.discount_code,
            priority: 2
          };
          validDiscList.push(itemValidDisc);
        }
        // untuk item_category
        else if (item.id_item_category === data.id_item_category) {
          itemValidDisc = {
            id_discount_management: data.id_discount_management,
            discount_code: data.discount_code,
            priority: 3
          };
          validDiscList.push(itemValidDisc);
        }
        // untuk plu, sku, category kosong semua
        else if (!data.item_plu && !data.item_sku && !data.id_item_category) {
          // console.log("into not categorized discount");
          itemValidDisc = {
            id_discount_management: data.id_discount_management,
            discount_code: data.discount_code,
            priority: 4
          };
          validDiscList.push(itemValidDisc);
        }
      });

      //   sorting validDiscList based on priority
      function compare(a, b) {
        if (a.priority < b.priority) {
          return -1;
        } else if (a.priority > b.priority) {
          return 1;
        } else return 0;
      }

      validDiscList.sort(compare);

      item.validDiscList = validDiscList;

      // item yang mendapat validDiscList
      if (item.validDiscList && item.validDiscList.length > 0) {
        for (var i = 0; i < item.validDiscList.length; i++) {
          var result = this.calcDiscItem(
            item.validDiscList[i].id_discount_management,
            item
          );
          // item yang menemukan diskon dalam validDiscList
          if (Boolean(parseFloat(result))) {
            item.id_discount = item.validDiscList[i].id_discount_management;
            item.discount_code = item.validDiscList[i].discount_code;
            item.discount = result;
            item.discount_code_active = item.validDiscList[i].discount_code;
            break;
          } else {
            // item yang tidak menemukan diskon dalam validDiscList
            item.id_discount = null;
            item.discount = item.item_discount ? item.item_discount : "0%";
            item.discount_code = "-";
            item.discount_code_active = item.discount_code;
          }
        }
      } else {
        // item yang tidak mendapat validDiscList
        item.id_discount = null;
        item.discount = item.item_discount ? item.item_discount : "0%";
        item.discount_code = "-";
        item.discount_code_active = item.discount_code;
      }
    }
    // if discountItems doesn't exist
    else {
      item.id_discount = null;
      item.discount = item.item_discount ? item.item_discount : "0%";
      item.discount_code = "-";
      item.discount_code_active = item.discount_code;
    }

    return item;

    // --------------- /discount management
  };

  handleItemDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleItemDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotQty = 0;
    let new_sum_subtotal_discount_item = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotQty += parseFloat(currData.base_qty);
      new_sum_subtotal_discount_item += getSubTotalPerItem(
        currData.item_price,
        currData.qty,
        currData.discount
      )
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalQty: newTotQty,
      sum_subtotal_discount_item: new_sum_subtotal_discount_item
    });
    handleSaveItemList(newData, newTotQty, new_sum_subtotal_discount_item, undefined, true);
  };

  handleSave = row => {
    const { handleSaveItemList, inclusive_tax, custDisc, discountItems } = this.props;
    const newData = this.state.dataSource;
    let newTotQty = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    if (row.id_unit !== newData[index].id_unit) {
      const price_list = getSalesPriceAndDiscountNonDM(row);
      row.item_price = price_list.price;
      row.item_discount = price_list.discount;
    } else {
      row.item_discount = row.item_discount ? row.item_discount : "0";
    }

    // --------------- discount management
    row = getItemDiscountDM(row, discountItems);
    // --------------- /discount management

    if (!row.id_discount) {
      row.id_discount = null;
    }

    // Ubah price dan base_qty
    // row.item_price = getItemPrice(row);
    row.unit_conv = getUnitConversion(row.id_unit, row.unit_conversion);
    row.base_qty = row.qty * row.unit_conv;
    row.sub_total = getSubTotalPerItem(row.item_price, row.qty, row.discount);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // generate sub_total_tax
    let sub_total_tax;
    if (inclusive_tax) {
      sub_total_tax = getSubTotalTaxPerItemInclusive(
        row,
        newData,
        custDisc,
        inclusive_tax
      );
    } else {
      sub_total_tax = getSubTotalTaxPerItem(
        row,
        newData,
        custDisc,
        inclusive_tax
      );
    }
    row.sub_total_tax = sub_total_tax;

    row.netto_sales_price = getNettPurchPrice(
      row,
      newData,
      inclusive_tax,
      custDisc
    );

    row.bill_discount = getDiscountBillPerItem(row, newData, custDisc);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    let new_sum_subtotal_discount_item = 0;
    newData.map(currData => {
      newTotQty += parseFloat(currData.base_qty);
      new_sum_subtotal_discount_item += getSubTotalPerItem(
        currData.item_price,
        currData.qty,
        currData.discount
      )
    });

    // console.log(row.base_qty);

    this.setState({
      dataSource: [...newData],
      totalQty: newTotQty,
      sum_subtotal_discount_item: new_sum_subtotal_discount_item
    });
    handleSaveItemList(newData, newTotQty, new_sum_subtotal_discount_item, undefined, true);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const raw_columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%"
        // fixed: "left"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "10%",
        // fixed: "left",
        render: text => {
          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "quantity" }),
        dataIndex: "qty",
        width: "10%",
        align: "right",
        editable: true
      },
      {
        title: getLanguage({ id: "unit" }),
        width: "10%",
        dataIndex: "unit_code",
        align: "center",
        editable: true
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="baseQty" /> </a>
            {/* <br />
            <Icon type="question-circle" /> */}
          </Tooltip>
        ),
        width: "10%",
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.base_unit_code}</>
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        width: "10%",
        align: "right",
        editable: false,
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "tax" }),
        dataIndex: "tax_name",
        width: "5%",
        align: "center",
        editable: true
        // render: (text, record) => {
        //   if (!text) {
        //     if (this.props.taxes.length > 0) {
        //       const findTax = this.props.taxes.filter(function(data) {
        //         return data.id_tax === record.id_tax;
        //       })[0];
        //       return findTax.tax_name;
        //     }
        //   } else return text;
        // }
      },
      {
        title: getLanguage({ id: "discount" }),
        dataIndex: "discount",
        width: "5%",
        align: "right",
        editable: false,
        render: (text, row) => {
          if (text.slice(-1) === "%") {
            return <><NumberFormat value={parseFloat(text)} />%</>;
          }
          else {
            if (row.sub_total < 0 && parseFloat(row.discount) > row.sub_total) {
              return (
                <>
                  <NumberFormat value={text} />
                  <br />
                  <IntlMessages id="discountExceed">
                    {txt => <span style={{ color: "#f5222d", fontSize: ".9em" }}>{txt}</span>}
                  </IntlMessages>
                </>
              )
            }
            return (<NumberFormat value={text} />);
          }
        }
      },
      {
        title: getLanguage({ id: "discountManagements.discountCode" }),
        dataIndex: "discount_code",
        width: "5%",
        align: "center",
        editable: false,
        render: text => {
          if (text) return text;
          else return "-";
        }
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        width: "10%",
        // editable: true,
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "netSalesPrice" }),
        dataIndex: "netto_sales_price",
        width: "15%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (
            !this.props.transaction_status ||
            (this.props.transaction_status === 4 ||
              this.props.transaction_status === 6)
          ) {
            return (
              <div>
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleItemDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>  
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleItemDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];

    const columns = raw_columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          taxes: this.props.taxes,
          handleSave: this.handleSave,
          transaction_status: this.props.transaction_status
        })
      };
    });
    return (
      <CardWrapper>
        <div>
          <ModalItemDetail
            visible={visible}
            selectedData={selectedItem}
            transaction_status={this.props.transaction_status}
            handleItemDetailModal={this.handleItemDetailModal}
            handleItemDetail={this.handleItemDetail}
            submit={this.handleItemDetail}
            departments={this.props.departments}
            projects={this.props.projects}
          />

          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              columns={columns}
              components={components}
              dataSource={dataSource}
              loading={this.props.loadingTable}
              pagination={false}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
            // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
