import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/sales/salesOrder/actions";
import notification from "../../../../components/notification";

import Button from "../../../../components/uielements/button";
import IntlMessages from "../../../../components/utility/intlMessages";
import {
  AutoComplete,
  // Button as ButtonAntd,
  // Checkbox,
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  // InputNumber,
  Radio,
  Row,
  Select,
  Spin,
  // Steps,
  Tabs,
  Tooltip
} from "antd";

import EditableTable from "./editableTable";
import ModalTransaction from "./modal";
// import ModalItemRequest from "./modalItemRequest";
// import ModalDownPayment from "./modalDownPayment";
// import ModalFreightCost from "./modalFreightCost";
// import { Object } from "core-js";
import {
  getExlDiscTax,
  getSalesPriceAndDiscountNonDM,
  getSupplierDiscount,
  getTotalAmount,
  getTotalDiscount,
  getTotalTax,
  convertDiscountMask,
  setSaveData,
  getSaveData,
  removeSaveData
} from "../../../../helpers/helper";
import { StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import AddModule from "../../../App/AddModule";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const TabPane = Tabs.TabPane;

const { getCustomerContacts, getItemsForSO, getUpdateSODetails } = actions;

class SalesOrderCreateForm extends Component {
  state = {
    activeCustDisc: null, // indicate which discount bill (discount code) is being used
    calcDiscItem: false,
    copy_trans: false,
    clear: false,
    customer: {},
    custDisc: "0", // dinamic discount bill
    cust_disc: "", // fixed disc per cust from cust foreign keys
    customerContactsName: [],
    discBill: null, //selected discount bill's detail
    discount_amount: 0,
    exlcDiscTax: 0,
    freight_include_in_total: 0,
    fullDiscMultiplier: null, //printed of full discount string for discount multiplier
    idGeneralCustomer: null,
    id_freight_account: null,
    id_location: null,
    id_customer: null,
    inclusive_tax: false,
    initIdLocation: true,
    item: null,
    items: [],
    limitInitItems: 0,
    limitFindGeneralCust: 0,
    netPayDay: null,
    netPayDisc: 0,
    itemDetails: {
      sales_order_details: []
    },
    paymentCategory: {
      id_payment_category: null,
      payment_type: null
    },
    soDetails: {
      so_down_payment: null
    },
    search: null,
    restore: null,
    total_amount: 0,
    total_discount: 0,
    total_exclude_discount_tax: 0,
    sum_subtotal_discount_item: 0,
    total_tax: 0,
    total_freight_cost: 0,
    total_qty: 0,
    transDate: moment().format("YYYY-MM-DD HH:mm:ss"),
    viewed: false,
    visible: false,
    visibleItemReq: false,
    visibleDownPayment: false,
    visibleFreightCost: false,
    err_discount: false,
    err_item: false
  };

  componentDidMount = () => {
    /* Init Restore Autosave */
    let restore = getSaveData("so");
    if (restore) {
      restore.fields.transaction_date = moment();
      restore.fields.due_date = moment(restore.fields.due_date);
      restore.fields.ship_date = restore.fields.ship_date
        ? moment(restore.fields.ship_date)
        : null;
      restore.fields.customer_po_date = restore.fields.customer_po_date
        ? moment(restore.fields.customer_po_date)
        : null;
      this.setState({ restore: restore });
    }
    /* END Init Restore Autosave */
  };

  componentDidUpdate(prevProps, prevState) {
    const {
      customer,
      customerContactsName,
      id_customer,
      idGeneralCustomer,
      id_location,
      inclusive_tax,
      itemDetails,
      netPayDay,
      netPayDisc,
      paymentCategory
    } = this.state;
    const prevSalesOrder = prevProps.salesOrder;
    const { salesOrder } = this.props;
    const { getFieldValue, getFieldsValue } = this.props.form;

    /* Restore Autosave */
    if (this.state.restore && prevProps.salesOrder.loadingForeign) {
      const { restore } = this.state;
      const { setFieldsValue } = this.props.form;
      setFieldsValue(restore.fields);

      // console.log(restore);
      this.setState(
        {
          customer: restore.customer,
          customerContactsName: restore.customerContactsName,
          id_customer: restore.id_customer,
          limitFindGeneralCust: 1,
          idGeneralCustomer: restore.idGeneralCustomer,
          id_location: restore.id_location,
          inclusive_tax: restore.inclusive_tax,
          itemDetails: { sales_order_details: restore.sales_order_details },
          calcDiscItem: true,
          netPayDay: restore.netPayDay,
          netPayDisc: restore.netPayDisc,
          paymentCategory: restore.paymentCategory,
          restore: null
        },
        () =>
          this.getUpdateSalesOrderDetails(
            restore.id_customer,
            restore.id_location,
            false,
            restore.paymentCategory.id_payment_category
          )
      );
    }
    /* End Restore Autosave */

    /* Auto Save */
    let data = Object.assign({}, { fields: getFieldsValue() }, itemDetails, {
      customer,
      customerContactsName,
      id_customer,
      idGeneralCustomer,
      id_location,
      inclusive_tax,
      netPayDay,
      netPayDisc,
      paymentCategory
    });

    data.fields.transaction_date = data.fields.transaction_date
      ? data.fields.transaction_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    data.fields.due_date = data.fields.due_date
      ? data.fields.due_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    data.fields.ship_date = data.fields.ship_date
      ? data.fields.ship_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    data.fields.customer_po_date = data.fields.customer_po_date
      ? data.fields.customer_po_date.format("YYYY-MM-DD")
      : null;

    if (
      itemDetails.sales_order_details !==
      prevState.itemDetails.sales_order_details
    ) {
      setSaveData("so", data);
    }

    /* END Autosave */

    // set idLocation
    const { user } = this.props;
    let { default_employee_location } = user ? user : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    if (
      default_employee_location.id_location &&
      this.state.initIdLocation === true
    ) {
      this.handleSelectLocation(default_employee_location.id_location);
      this.setState({
        id_location: default_employee_location.id_location,
        initIdLocation: false
      });
    }

    // set general customers
    const { customers } = salesOrder;
    if (
      customers &&
      customers.length > 0 &&
      this.state.limitFindGeneralCust === 0
    ) {
      const findGeneralCust = customers.find(data => data.is_default === 1);
      if (findGeneralCust) {
        this.handleChangeCustomer(findGeneralCust.id_customer);
        this.setState({
          idGeneralCustomer: findGeneralCust.id_customer,
          limitFindGeneralCust: 1
        });
      } else
        this.setState({
          idGeneralCustomer: null,
          limitFindGeneralCust: 1
        });
    }

    if (
      this.state.id_location &&
      this.state.id_customer &&
      getFieldValue("id_payment_category") &&
      this.state.limitInitItems === 0
    ) {
      const { getItemsForSO } = this.props;
      // this.handleChangeCustomer(this.state.id_customer, true);
      getItemsForSO(
        this.state.id_customer,
        this.state.id_location,
        getFieldValue("transaction_date").format("YYYY-MM-DD HH:mm:ss"),
        getFieldValue("id_payment_category")
      );
      this.setState({ limitInitItems: 1 });
    }

    // if get discount bill on loc/pay/cust changed
    if (
      salesOrder &&
      prevSalesOrder.discountBills !== salesOrder.discountBills
    ) {
      const { discountBills } = salesOrder;
      // if get discountBill
      // const { total_amount } = this.state;
      if (discountBills.length > 0) {
        this.setState({
          discBill: discountBills[0]
        });
      }
      // if doesnt get any discountBill
      else {
        const { customer } = this.state;
        const disc = customer ? parseFloat(customer.discount) * 100 + "%" : 0;
        this.setState({
          activeCustDisc: null,
          custDisc: disc ? disc : "0%",
          discBill: null,
          fullDiscMultiplier: null
        });
        this.props.form.setFieldsValue({
          discount: disc ? convertDiscountMask(disc) : "0%"
        });
      }
    }

    // when loc/cust/pay changed
    if (
      salesOrder.update_sales_order_details !==
      prevSalesOrder.update_sales_order_details
    ) {
      const { setFieldsValue } = this.props.form;
      const { update_sales_order_details, SOTransactionDate } = salesOrder;
      if (update_sales_order_details.length === 0) {
        this.setState({
          clear: true,
          itemDetails: { sales_order_details: [] }
        });
      } else {
        this.setState({
          calcDiscItem: true,
          items: update_sales_order_details
        });
      }
      setFieldsValue({
        transaction_date: moment(SOTransactionDate, "YYYY-MM-DD HH:mm:ss")
      });
    }

    // setelah copy transaction
    if (
      this.state.copy_trans &&
      this.state.itemDetails.sales_order_details.length > 0
    ) {
      const {
        id_customer,
        id_location,
        id_payment_category
      } = getFieldsValue();
      this.getUpdateSalesOrderDetails(
        id_customer,
        id_location,
        null,
        id_payment_category
      );
      this.setState({ copy_trans: false });
    }
  }

  componentWillUnmount = () => {
    this.setState({
      limitFindGeneralCust: 0,
      limitInitItems: 0
    });
  };

  // calculating discount bill
  calcDiscBill = total_amount => {
    const { discBill } = this.state;
    const { discountBills } = this.props.salesOrder;

    const checkDiscount = (
      discBill,
      discType,
      total_amount,
      discount_management_details
    ) => {
      const { setFieldsValue } = this.props.form;
      // for discount_type 3
      if (discType === 3) {
        const findIndexDisc = discount_management_details.findIndex(
          data => parseFloat(total_amount) < parseFloat(data.min_qty_amount)
        );
        if (discount_management_details[findIndexDisc - 1]) {
          // passing the disc
          let discount_value =
            discount_management_details[findIndexDisc - 1].discount_value;
          this.setState({
            activeCustDisc: discBill
              ? discBill.discount_code
              : discountBills[0].discount_code,
            custDisc: discount_value
          });
          const e = {
            target: {
              value: discount_value
            }
          };
          this.handleChangeCustDisc(e);
          setFieldsValue({
            discount: convertDiscountMask(discount_value)
          });
        } else {
          // if total_amount is passing the highest min_amount
          if (
            discount_management_details[discount_management_details.length - 1]
              .min_qty_amount <= total_amount
          ) {
            let discount_value =
              discount_management_details[
                discount_management_details.length - 1
              ].discount_value;
            this.setState({
              activeCustDisc: discBill.discount_code,
              custDisc: discount_value
            });
            const e = {
              target: {
                value: discount_value
              }
            };
            this.handleChangeCustDisc(e);
            setFieldsValue({
              discount: convertDiscountMask(discount_value)
            });
          }
          // doesnt get any discount, doesn't pass any min_amount
          else {
            const { customer } = this.state;
            const disc = customer
              ? parseFloat(customer.discount) * 100 + "%"
              : 0;
            this.setState({
              activeCustDisc: null,
              custDisc: disc ? disc : "0%",
              fullDiscMultiplier: null
            });
            const e = {
              target: {
                value: disc ? disc : "0%"
              }
            };
            this.handleChangeCustDisc(e);
            setFieldsValue({
              discount: disc ? convertDiscountMask(disc) : "0%"
            });
          }
        }
      }
      // for discount_type 4
      else if (discType === 4) {
        var string = "";
        if (total_amount > 0) {
          // print the discount
          discount_management_details.map((data, index) => {
            if (index === discount_management_details.length - 1) {
              string += data.discount_value;
            } else {
              string += data.discount_value + "+";
            }
          });

          this.setState({
            fullDiscMultiplier: string
          });
          if (string.length > 8) {
            string = string.slice(0, 8) + "...";
          }
          setFieldsValue({
            discount: convertDiscountMask(string)
          });

          // create var for custDisc multiplier
          var custDiscMul,
            multi = 1;

          // calc the multiplier disc using map
          discount_management_details.map((data, index) => {
            // ------------------------
            multi *= 1 - parseFloat(data.discount_value) / 100;
            // ------------------------
          });
          custDiscMul = `${(1 - multi) * 100}%`;

          const e = {
            target: { value: custDiscMul }
          };

          this.setState({
            activeCustDisc: discBill.discount_code,
            custDisc: custDiscMul
          });

          this.handleChangeCustDisc(e);
        } else {
          const { customer } = this.state;
          const disc = customer ? parseFloat(customer.discount) * 100 + "%" : 0;
          string = disc ? disc : "0%";

          this.setState({
            activeCustDisc: null,
            custDisc: disc ? disc : "0%",
            fullDiscMultiplier: null
          });

          setFieldsValue({
            discount: convertDiscountMask(string)
          });

          const e = {
            target: { value: string }
          };
          this.handleChangeCustDisc(e);
        }
      }
    };

    if (discBill || discountBills[0]) {
      const { discount_management_details } = discBill
        ? discBill
        : discountBills[0];
      const discType = discBill
        ? discBill.discount_type
        : discountBills[0].discount_type;
      checkDiscount(
        discBill ? discBill : discountBills[0],
        discType,
        total_amount,
        discount_management_details
      );
    }
  };

  getUpdateSalesOrderDetails = (
    idCustomer,
    idLocation,
    changed = false,
    idPaymentCategory
  ) => {
    const { itemDetails } = this.state;
    const { getFieldValue } = this.props.form;
    const transaction_date = getFieldValue("transaction_date").format(
      "YYYY-MM-DD HH:mm:ss"
    );

    let values = {
      id_customer: idCustomer,
      id_location: idLocation,
      id_payment_category: idPaymentCategory,
      transaction_date
    };

    const data = Object.assign({}, values, itemDetails);
    this.props.getUpdateSODetails(data);
  };

  handleChangePaymentCategory = value => {
    const { getItemsForSO } = this.props;
    const { setFieldsValue } = this.props.form;
    const { paymentCategories } = this.props.salesOrder;
    const { id_customer, id_location, transDate } = this.state;
    setFieldsValue({ id_payment_term: undefined, due_date: undefined });

    const paymentCategory = paymentCategories.find(
      data => data.id_payment_category === value
    );
    this.setState({
      paymentCategory
    });

    if (id_customer && id_location) {
      const { sales_order_details } = this.state.itemDetails;
      if (sales_order_details.length > 0) {
        this.getUpdateSalesOrderDetails(id_customer, id_location, null, value);
      } else {
        getItemsForSO(id_customer, id_location, transDate, value);
      }
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      let { itemDetails, soDetails } = this.state;
      const {
        custDisc,
        fullDiscMultiplier,
        netPayDisc,
        netPayDay,
        total_qty,
        discount_amount,
        total_tax,
        total_exclude_discount_tax,
        total_discount,
        total_amount
      } = this.state;
      const { employees } = this.props.salesOrder;

      // validasi item
      if (itemDetails.sales_order_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      // cek diskon item
      let counter = 0;
      var item;
      for (item of itemDetails.sales_order_details) {
        if (item.sub_total < 0 && parseFloat(item.discount) > item.sub_total) {
          counter++;
          break;
        }
      }

      if (counter > 0) {
        this.setState({ err_discount: true });
        return;
      } else {
        this.setState({ err_discount: false });
      }

      if (!soDetails.so_down_payment || !soDetails.so_down_payment.amount) {
        soDetails.so_down_payment = null;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      values.due_date = values.due_date
        ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      values.ship_date = values.ship_date
        ? values.ship_date.format("YYYY-MM-DD HH:mm:ss")
        : null;

      values.customer_po_date = values.customer_po_date
        ? values.customer_po_date.format("YYYY-MM-DD")
        : null;

      // freight cost setup
      values.total_freight_cost
        ? (values.total_freight_cost = parseFloat(
            values.total_freight_cost.replace(/\$\s?|(,*)/g, "")
          ))
        : (values.total_freight_cost = 0);
      values.id_freight_account = this.state.id_freight_account;
      values.freight_include_in_total = this.state.freight_include_in_total;

      values.customer_credit_limit = parseFloat(
        values.customer_credit_limit.replace(/\$\s?|(,*)/g, "")
      );

      // salesman set up
      if (values.id_salesman) {
        const findSalesman = employees.find(function(data) {
          return data.id_employee === values.id_salesman;
        });

        values.salesman_name =
          findSalesman.first_name +
          (findSalesman.last_name ? " " + findSalesman.last_name : "");
      } else values.id_salesman = null;

      const so_deliv = {
        so_delivery: {
          ship_date: values.ship_date ? values.ship_date : null,
          ship_to: values.ship_to ? values.ship_to : null
        }
      };

      // handle bill discount
      values.id_discount = this.state.activeCustDisc
        ? this.state.discBill.id_discount_management
        : null;
      values.discount =
        values.id_discount && fullDiscMultiplier
          ? fullDiscMultiplier
          : custDisc;

      // set up itemDetails
      itemDetails.sales_order_details.map(data => (data.delivered_qty = 0));

      const data = Object.assign({}, values, itemDetails, soDetails, so_deliv, {
        total_qty: total_qty,
        discount_amount: discount_amount,
        total_tax: total_tax,
        total_exclude_discount_tax: total_exclude_discount_tax,
        total_discount: total_discount,
        total_amount: total_amount,
        net_payment_day: netPayDay,
        net_payment_discount: netPayDisc,
        inclusive_tax: values.inclusive_tax ? values.inclusive_tax : false
      });

      if (!err) {
        // console.log("submitted data ", data);
        this.props.submit(data);
      } else {
        // console.log(err);
      }
    });
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  // handleModalDownPayment = () => {
  //   const { visibleDownPayment } = this.state;
  //   visibleDownPayment
  //     ? this.setState({ visibleDownPayment: false })
  //     : this.setState({ visibleDownPayment: true });
  // };

  handleModalFreightCost = () => {
    const { visibleFreightCost } = this.state;
    visibleFreightCost
      ? this.setState({ visibleFreightCost: false })
      : this.setState({ visibleFreightCost: true });
  };

  handleSelectLocation = id_location => {
    const { getItemsForSO } = this.props;
    const { getFieldValue } = this.props.form;
    const { id_customer, paymentCategory } = this.state;

    this.setState({
      id_location: id_location
      // clear: this.state.id_location !== id_location ? true : false
    });
    if (id_customer) {
      const transDate = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const { sales_order_details } = this.state.itemDetails;
      if (sales_order_details.length > 0) {
        this.getUpdateSalesOrderDetails(
          id_customer,
          id_location,
          null,
          paymentCategory.id_payment_category
        );
      } else {
        getItemsForSO(
          id_customer,
          id_location,
          transDate,
          paymentCategory.id_payment_category
        );
      }
    }
  };

  handleSelectItem = value => {
    if (value) {
      const { infoMessages, items } = this.props.salesOrder;
      const { getItemsForSO } = this.props;
      const {
        search,
        id_customer,
        id_location,
        paymentCategory,
        transDate
      } = this.state;
      const item = items.find(item => item.id_item === value);
      item.id_unit = item.preference_item.id_sales_unit;
      const price_list = getSalesPriceAndDiscountNonDM(item);
      // if ((item.item_type !== 4 && item.item_type !== 3) && parseFloat(item.current_qty) <= 0) {
      //   notification("error", infoMessages.not_available_item);
      // } else
      if (price_list.price <= 0) {
        notification("error", infoMessages.not_available_price);
      } else {
        item.price = price_list.price;
        item.discount = price_list.discount;
        this.setState({
          item
        });
      }

      if (search) {
        getItemsForSO(
          id_customer,
          id_location,
          transDate,
          paymentCategory.id_payment_category
        );
        this.setState({ search: null });
      }
    }
  };

  handleSelectModal = value => {
    const { getItemsForSO } = this.props;
    const { transactions } = this.props.salesOrder;
    const { setFieldsValue } = this.props.form;
    const { transDate, paymentCategory } = this.state;
    const findTransaction = transactions.find(
      curr_data => curr_data.id_sales_order === value
    );
    let transaction = Object.assign({}, findTransaction);

    transaction.sales_order_details.map(data => {
      data.id_department = null;
      data.id_project = null;
      data.item_note = null;
      data.id_sales_invoice = null;
      data.id_sales_invoice_detail = null;
      data.id_sales_order = null;
      data.sales_order = {};
      data.id_delivery_order = null;
      data.id_delivery_order_detail = null;
      data.sales_order_no = null;
      data.delivery_order_no = null;
      data.delivery_order = {};
      data.delivery_order_detail = {};
      data.id_discount = null;
      data.discount_code = null;
      data.discount = "0%";
      data.discount_amount = 0;
    });

    this.setState({
      items: [...transaction.sales_order_details],
      calcDiscItem: true,
      copy_trans: true,
      // itemDetails: {
      //   sales_order_details: transaction.sales_order_details
      // },
      id_location: transaction.id_location,
      id_supplier: transaction.id_supplier,
      inclusive_tax: transaction.inclusive_tax ? true : false,
      netPayDay: transaction.net_payment_day,
      netPayDisc: transaction.net_payment_discount
    });
    setFieldsValue({
      customer_po_no: transaction.customer_po_no,
      id_location: transaction.id_location,
      id_customer: transaction.id_customer
    });
    if (transaction.inclusive_tax) {
      setFieldsValue({
        inclusive_tax: 1
      });
    }
    if (transaction.customer_po_date) {
      setFieldsValue({
        customer_po_date: moment(transaction.customer_po_date, "YYYY-MM-DD")
      });
    }
    if (transaction.id_salesman) {
      setFieldsValue({
        id_salesman: transaction.id_salesman
      });
    }
    if (transaction.ship_date) {
      setFieldsValue({
        ship_date: moment(transaction.ship_date, "YYYY-MM-DD HH:mm:ss")
      });
    }
    if (transaction.ship_to) {
      setFieldsValue({
        ship_to: transaction.ship_to
      });
    }
    this.handleChangeCustomer(transaction.id_customer);
    this.handleSelectLocation(transaction.id_location);
    getItemsForSO(
      transaction.id_customer,
      transaction.id_location,
      transDate,
      paymentCategory.id_payment_category
    );
    this.handleModal();
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { setFieldsValue, getFieldValue } = this.props.form;
      const { getItemsForSO } = this.props;
      const { id_location, id_customer, paymentCategory } = this.state;
      const { paymentTerms } = this.props.salesOrder;
      const paymentTerm = paymentTerms.find(
        data => data.id_payment_term === getFieldValue("id_payment_term")
      );
      const transDate = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const due_date = paymentTerm
        ? moment(transDate).add(paymentTerm.net_payment_day, "d")
        : null;
      this.setState({ due_date });
      setFieldsValue({ due_date: due_date });
      if (id_location && id_customer) {
        getItemsForSO(
          id_customer,
          id_location,
          transDate,
          paymentCategory.id_payment_category
        );
        if (this.state.itemDetails.sales_order_details.length > 0) {
        }
      }
      this.setState({ transDate });
    }
  };

  handleChangeCustomer = (
    id_customer,
    defendPay = false,
    update_price = true
  ) => {
    if (id_customer) {
      let setStateData = {};
      const { id_location } = this.state;
      const { setFieldsValue, getFieldValue } = this.props.form;
      const { getCustomerContacts, getItemsForSO } = this.props;
      const {
        customers,
        paymentCategories,
        paymentTerms
      } = this.props.salesOrder;
      const customer = customers.find(data => data.id_customer === id_customer);
      const paymentCategory = paymentCategories.find(
        data => data.id_payment_category === customer.id_payment_category
      );
      const paymentTerm = paymentTerms.find(
        data => data.id_payment_term === customer.id_payment_term
      );
      const due_date = paymentTerm
        ? moment(transDate).add(paymentTerm.net_payment_day, "d")
        : null;
      // store data customer contact ke state
      if (customer.customer_contacts.length > 0) {
        const customerFindContact = [];
        for (var i = 0; i < customer.customer_contacts.length; i++) {
          customerFindContact.push(
            customer.customer_contacts[i].customer_contact_name
          );
        }
        setStateData.customerContactsName = customerFindContact;
      } else {
        setStateData.customerContactsName = [];
      }
      const transDate = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );

      const disc = customer.discount
        ? parseFloat(customer.discount) * 100 + "%"
        : 0;

      if (id_location) {
        const { sales_order_details } = this.state.itemDetails;
        if (sales_order_details.length > 0 && update_price) {
          this.getUpdateSalesOrderDetails(
            id_customer,
            id_location,
            null,
            customer.id_payment_category
          );
        } else {
          getItemsForSO(
            id_customer,
            id_location,
            transDate,
            customer.id_payment_category
          );
        }
      }
      getCustomerContacts(id_customer);

      setStateData.clear = false;
      setStateData.id_customer = id_customer;
      setStateData.customer = customer;

      if (this.state.paymentCategory.id_payment_category && defendPay) {
        // if user already changed the payment method
      } else {
        setStateData.due_date = due_date;
        setStateData.netPayDay = paymentTerm ? paymentTerm.net_payment_day : 0;
        setStateData.netPayDisc = paymentTerm ? paymentTerm.discount : 0;
        setStateData.paymentCategory = paymentCategory;

        setFieldsValue({
          id_payment_category: customer.id_payment_category,
          id_payment_term: customer.id_payment_term,
          due_date: due_date
        });
      }
      const e = {
        target: { value: disc }
      };
      this.handleChangeCustDisc(e);
      setFieldsValue({
        customer_credit_limit: parseFloat(customer.credit_limit)
          .toFixed(2)
          .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
        contact_person: null,
        // id_currency: currency ? currency.id_currency : null,
        // currency_rate: parseFloat(currRate).toFixed(2),
        // fiscal_rate: parseFloat(fiscRate).toFixed(2),
        address_name: [],
        discount: convertDiscountMask(disc),
        phone: null,
        ship_to: null
      });

      this.setState(setStateData);
    }
  };

  handleChangePaymentTerm = id_payment_term => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { paymentTerms } = this.props.salesOrder;
    const paymentTerm = paymentTerms.find(
      data => data.id_payment_term === id_payment_term
    );
    const transDate = getFieldValue("transaction_date").format(
      "YYYY-MM-DD HH:mm:ss"
    );
    const due_date = paymentTerm
      ? moment(transDate).add(paymentTerm.net_payment_day, "d")
      : null;
    this.setState({
      due_date,
      netPayDay: paymentTerm.net_payment_day,
      netPayDisc: paymentTerm.discount
    });
    setFieldsValue({ due_date: due_date });
  };

  handleCustomerContact = value => {
    const { customerContactsName, id_customer } = this.state;
    const { customers } = this.props.salesOrder;
    const { setFieldsValue } = this.props.form;
    if (customerContactsName.length > 0) {
      const findCust = customers.find(data => data.id_customer === id_customer);
      const findCustCont = findCust.customer_contacts.find(
        data => data.customer_contact_name === value
      );
      if (findCustCont) {
        setFieldsValue({
          phone: findCustCont.phone
        });
      } else {
        setFieldsValue({
          phone: null
        });
      }
    } else {
      setFieldsValue({
        phone: null
      });
    }
  };

  handleInclusiveTax = e => {
    const { inclusive_tax } = this.state;
    this.setState({ inclusive_tax: !inclusive_tax });
  };

  handleSearchItem = value => {
    const { getItemsForSO } = this.props;
    const { id_customer, id_location, paymentCategory } = this.state;
    const { getFieldValue } = this.props.form;
    // console.log(this.props.salesOrder);
    this.setState({ search: value });
    if (id_customer && id_location) {
      const transDate = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );
      getItemsForSO(
        id_customer,
        id_location,
        transDate,
        paymentCategory.id_payment_category,
        value
      );
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleChangeCustDisc = e => {
    const { itemDetails, inclusive_tax } = this.state;
    const { sales_order_details } = itemDetails;
    // const { setFieldsValue } = this.props.form;

    const value = e.target.value ? e.target.value : "0";

    this.setState({
      custDisc: value,
      // cust_disc: value,
      discount_amount: getSupplierDiscount(sales_order_details, value),
      total_tax: getTotalTax(sales_order_details, value, inclusive_tax),
      total_exclude_discount_tax: getExlDiscTax(
        sales_order_details,
        inclusive_tax,
        value
      ),
      total_discount: getTotalDiscount(
        sales_order_details,
        value,
        inclusive_tax
      ),
      total_amount: getTotalAmount(sales_order_details, value, inclusive_tax)
    });
    // setFieldsValue({ discount: value });
  };

  handleSaveItemList = (items, total_qty, sum_subtotal_discount_item) => {
    const { custDisc, discBill, inclusive_tax } = this.state;

    if (discBill) {
      this.setState(
        {
          clear: false,
          itemDetails: { sales_order_details: items },
          total_qty: total_qty,
          sum_subtotal_discount_item
        },
        () => this.calcDiscBill(sum_subtotal_discount_item)
      );
    } else {
      const total_amount = getTotalAmount(items, custDisc, inclusive_tax);
      this.setState({
        clear: false,
        discount_amount: getSupplierDiscount(items, custDisc),
        itemDetails: { sales_order_details: items },
        total_qty: total_qty,
        sum_subtotal_discount_item,
        total_tax:
          items.length > 0 ? getTotalTax(items, custDisc, inclusive_tax) : 0,
        total_exclude_discount_tax:
          items.length > 0 ? getExlDiscTax(items, inclusive_tax, custDisc) : 0,
        total_discount: getTotalDiscount(items, custDisc, inclusive_tax),
        total_amount
      });
    }
  };

  // handleOkDownPayment = (value, remainClosed) => {
  //   const { setFieldsValue } = this.props.form;
  //   setFieldsValue({ amount: value.amount });
  //   value.amount = parseFloat(value.amount.replace(/\$\s?|(,*)/g, ""));

  //   let so_down_payment = null;
  //   if (!value.id_cash_bank) {
  //     so_down_payment = null;
  //   } else {
  //     so_down_payment = {
  //       ...this.state.soDetails.so_down_payment,
  //       ...value
  //     };
  //   }

  //   this.setState({
  //     soDetails: {
  //       so_down_payment
  //     }
  //   });
  //   if (!remainClosed) {
  //     this.handleModalDownPayment();
  //   }
  // };

  handleOkFreightCost = value => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({ total_freight_cost: value.total_freight_cost });
    value.total_freight_cost = value.total_freight_cost
      ? parseFloat(value.total_freight_cost.replace(/\$\s?|(,*)/g, ""))
      : 0;

    this.setState({
      freight_include_in_total: value.freight_include_in_total,
      id_freight_account: value.id_freight_account,
      total_freight_cost: value.total_freight_cost
    });

    this.handleModalFreightCost();
  };

  // handleCancelDownPayment = () => {
  //   const { setFieldsValue } = this.props.form;
  //   setFieldsValue({ amount: "0.00" });
  //   this.setState({
  //     soDetails: {
  //       so_down_payment: null
  //     },
  //     visibleDownPayment: false
  //   });
  // };

  handleCancelFreightCost = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({ total_freight_cost: "0.00" });
    this.setState({
      freight_include_in_total: 0,
      id_freight_account: null,
      total_freight_cost: null,
      visibleFreightCost: false
    });
  };

  clearItem = (items, total_qty, sum_subtotal_discount_item) => {
    const { custDisc, discBill, inclusive_tax } = this.state;

    if (discBill) {
      this.setState(
        {
          item: null,
          itemDetails: { sales_order_details: items },
          total_qty: total_qty,
          sum_subtotal_discount_item
        },
        () => this.calcDiscBill(sum_subtotal_discount_item)
      );
    } else {
      const total_amount = getTotalAmount(items, custDisc, inclusive_tax);
      this.setState({
        discount_amount: getSupplierDiscount(items, custDisc),
        itemDetails: { sales_order_details: items },
        item: null,
        sum_subtotal_discount_item,
        total_amount,
        total_discount: getTotalDiscount(items, custDisc, inclusive_tax),
        total_exclude_discount_tax:
          items.length > 0 ? getExlDiscTax(items, inclusive_tax, custDisc) : 0,
        total_qty: total_qty,
        total_tax:
          items.length > 0 ? getTotalTax(items, custDisc, inclusive_tax) : 0
      });
    }
  };

  clearItems = (items, total_qty, sum_subtotal_discount_item) => {
    const { custDisc, discBill, inclusive_tax } = this.state;

    if (discBill) {
      this.setState(
        {
          clear: false,
          calcDiscItem: false,
          items: [],
          itemDetails: { sales_order_details: items },
          total_qty: total_qty,
          sum_subtotal_discount_item
        },
        () => this.calcDiscBill(sum_subtotal_discount_item)
      );
    } else {
      const total_amount = getTotalAmount(items, custDisc, inclusive_tax);
      this.setState({
        clear: false,
        calcDiscItem: false,
        discount_amount: getSupplierDiscount(items, custDisc),
        items: [],
        itemDetails: { sales_order_details: items },
        sum_subtotal_discount_item,
        total_amount,
        total_exclude_discount_tax:
          items.length > 0 ? getExlDiscTax(items, inclusive_tax, custDisc) : 0,
        total_discount: getTotalDiscount(items, custDisc, inclusive_tax),
        total_qty: total_qty,
        total_tax:
          items.length > 0 ? getTotalTax(items, custDisc, inclusive_tax) : 0
      });
    }
  };

  renderAddress = id_address => {
    const { setFieldsValue } = this.props.form;
    const { customerAddresses } = this.props.salesOrder;
    const findAdd = customerAddresses.find(
      data => data.id_customer_address === id_address
    );
    var string =
      findAdd.customer_address_name +
      " - " +
      findAdd.address +
      (findAdd.district ? ", " + findAdd.district.district_name + ", " : "") +
      (findAdd.city
        ? findAdd.city.city_type + " " + findAdd.city.city_name + ", "
        : "") +
      (findAdd.province ? findAdd.province.province_name + ", " : "") +
      (findAdd.country ? findAdd.country.country_name : "");
    if (findAdd.zip_code) {
      string += " " + findAdd.zip_code;
    }
    setFieldsValue({ ship_to: string });
  };

  render() {
    // console.log(this.props.salesOrder);
    const { search, visible } = this.state;
    const {
      getFieldDecorator,
      getFieldValue
      // setFieldsValue
    } = this.props.form;
    const { salesOrder } = this.props;
    const {
      // accounts,
      // cashBanks,
      // cashFlowTypes,
      customers,
      customerAddresses,
      departments,
      discountItems,
      employees,
      items,
      locations,
      paymentCategories,
      paymentTerms,
      projects,
      couriers,
      taxes
    } = salesOrder ? salesOrder : [];

    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingUpdateSalesOrderDetails,
      // loadingItemRequests,
      loadingForeign
      // id_location
    } = salesOrder ? salesOrder : [];
    // const data = salesOrder.salesOrder ? salesOrder.salesOrder : [];

    // const used = data.is_used;
    const error = salesOrder
      ? salesOrder.errorField
        ? salesOrder.errorField
        : []
      : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          salesOrder={salesOrder}
          idLocation={this.state.id_location}
          idCustomer={this.state.id_customer}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        {/* <ModalItemRequest
          visible={visibleItemReq}
          salesOrder={salesOrder}
          id_location={this.state.id_location}
          handleModal={this.handleModalItemRequest}
          handleSelectModal={this.handleSelectModalItemRequest}
        /> */}

        {/* <ModalFreightCost
          accounts={accounts}
          // handleCancelModal={this.handleCancelFreightCost}
          handleModal={this.handleModalFreightCost}
          salesOrder={salesOrder}
          submit={this.handleOkFreightCost}
          transactionStatus={4}
          visible={this.state.visibleFreightCost}
        /> */}

        {/* <ModalDownPayment
          accounts={accounts}
          cashBanks={cashBanks}
          cashFlowTypes={cashFlowTypes}
          dueDate={this.state.due_date}
          handleModal={this.handleModalDownPayment}
          id_location={this.state.id_location}
          loadingForeign={loadingForeign}
          salesOrder={{ salesOrder: this.state.soDetails }}
          submit={this.handleOkDownPayment}
          totalAmount={this.state.total_amount}
          transDate={this.state.transDate}
          transaction_status={4}
          visible={visibleDownPayment}
        /> */}

        {/* <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps size="small" progressDot={customDot} current={0}>
              <Step title="New" />
              <Step title="Ordered" />
              <Step title="Received" />
              <Step title="Closed" />
              <Step title="Cancelled" />
            </Steps>
          </Col>
        </Row> */}

        <Tabs defaultActiveKey="1">
          <TabPane tab={<IntlMessages id="salesOrders.tabs.detail" />} key={1}>
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="salesOrders.salesOrderNo.help" />
                            }
                          >
                            <IntlMessages id="salesOrders.copyPreviousSO" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        <Button
                          disabled={
                            loadingTransactions ||
                            !this.state.id_location ||
                            !this.state.id_customer ||
                            this.state.itemDetails.sales_order_details.length >
                              0
                          }
                          icon="select"
                          loading={loadingTransactions}
                          onClick={this.handleModal}
                          style={{ height: "31px", width: "100%" }}
                          // shape="circle"
                        >
                          <IntlMessages id="button.copy" />
                        </Button>
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="salesOrders.transactionDate" />
                        }
                        style={{ marginBottom: 0, paddingBottom: 0 }}
                      >
                        {getFieldDecorator("transaction_date", {
                          initialValue: moment()
                        })(
                          <DatePicker
                            style={{ width: "100%" }}
                            showTime
                            format="DD MMMM YYYY HH:mm:ss"
                            // onChange={this.handleChangeTransDate}
                            onOpenChange={this.handleChangeTransDate}
                            disabled={true}
                            allowClear={false}
                            disabledDate={e => e.valueOf() > moment()}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12} style={{ paddingRight: "5px" }}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.location" />}
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: default_employee_location.id_location,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "98.5%" }}
                            placeholder={
                              <IntlMessages id="purchaseOrders.filter.select.location" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleSelectLocation}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations.map((data, key) => (
                              <Option key={key} value={data.id_location}>
                                {`${data.location_code} (${data.location_name})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.customer" />}
                      >
                        {getFieldDecorator("id_customer", {
                          initialValue: this.state.idGeneralCustomer
                            ? this.state.idGeneralCustomer
                            : [],
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="salesOrders.filter.select.customer" />
                            }
                            loading={loadingForeign}
                            onSelect={(value) => this.handleChangeCustomer(value)}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {AddModule(
                              "Create Customer",
                              "/dashboard/setup/customers/add",
                              "customers.pageHeader",
                              this.props.history,
                              permissions
                            )}

                            {customers.map((data, key) => (
                              <Option key={key} value={data.id_customer}>
                                {`${data.customer_code} (${data.customer_name})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>

                <Col span={24}>
                  {/* form item untuk customer po no */}
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="salesOrders.customerPOInfo.no" />
                        }
                      >
                        {getFieldDecorator(
                          "customer_po_no",
                          {}
                        )(
                          <Input
                            placeholder={getLanguage({
                              id: "typeCustomerPONo"
                            })}
                            style={{ width: "100%" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="salesOrders.customerPOInfo.date" />
                        }
                      >
                        {getFieldDecorator(
                          "customer_po_date",
                          {}
                        )(
                          <DatePicker
                            disabledDate={e =>
                              e.valueOf() >
                              moment(this.state.transDate, "YYYY-MM-DD").endOf(
                                "day"
                              )
                            }
                            style={{ width: "100%" }}
                            format="DD MMMM YYYY"
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Row>
                  <Form.Item
                    {...formTextArea}
                    style={{ paddingBottom: "12px" }}
                    label={<IntlMessages id="salesOrders.note" />}
                  >
                    {getFieldDecorator("note", {
                      // initialValue: data.note
                    })(<TextArea autosize={{ minRows: 4 }} />)}
                  </Form.Item>
                </Row>
                <Row>
                  <Col span={24}>
                    <Col span={10} style={{ paddingRight: "10px" }}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.creditLimit" />}
                      >
                        {getFieldDecorator("customer_credit_limit", {
                          // initialValue: user ? user.username : null
                        })(
                          <Input
                            disabled={true}
                            //   style={{ textTransform: "uppercase" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    {/* <Col span={6}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="locations.add.status" />}
                      >
                        <StatusTag
                          className="new-receipt inside-form"
                          style={{ height: "32px", marginTop: "0px" }}
                        >
                          New
                        </StatusTag>
                      </Form.Item>
                    </Col> */}
                  </Col>
                </Row>
              </Col>
            </Row>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="itemDetails" />
            </Divider>

            <Row style={{ marginBottom: "40px" }}>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  validateStatus={
                    error.sales_order_details || this.state.err_item
                      ? "error"
                      : null
                  }
                  help={
                    error.sales_order_details ? (
                      error.sales_order_details
                    ) : this.state.err_item ? (
                      <IntlMessages id="itemDataEmpty" />
                    ) : null
                  }
                  extra={<IntlMessages id="salesOrders.item.help" />}
                >
                  <Select
                    value={search ? search : []}
                    showSearch
                    allowClear
                    // optionFilterProp="children"
                    filterOption={false}
                    placeholder={
                      <IntlMessages id="salesOrders.item.placeholder" />
                    }
                    loading={loadingItems}
                    notFoundContent={
                      loadingItems ? (
                        <Spin size="small" />
                      ) : this.state.id_location && this.state.id_customer ? (
                        <Empty description={<IntlMessages id="noData" />} />
                      ) : (
                        <Empty
                          description={
                            <IntlMessages id="salesOrders.item.noSelection" />
                          }
                        />
                      )
                    }
                    onSelect={this.handleSelectItem}
                    onChange={this.handleChangeItem}
                    onSearch={this.handleSearchItem}
                    optionFilterProp="children"
                    filterOption={true}
                    value={undefined}
                  >
                    {this.state.id_customer
                      ? AddModule(
                          "Create Item",
                          "/dashboard/setup/items/add",
                          "items.pageHeader",
                          this.props.history,
                          permissions
                        )
                      : null}

                    {this.state.id_location && this.state.id_customer
                      ? items.map(data => (
                          <Option key={data.id_item} value={data.id_item}>
                            {data.item_code + " - " + data.item_name}
                          </Option>
                        ))
                      : null}
                  </Select>
                </Form.Item>
              </Col>
              <Col span={24}>
                <EditableTable
                  calcDiscItem={this.state.calcDiscItem}
                  clear={this.state.clear}
                  clearItem={this.clearItem}
                  clearItems={this.clearItems}
                  departments={departments}
                  discountItems={discountItems}
                  handleSaveItemList={this.handleSaveItemList}
                  inclusive_tax={this.state.inclusive_tax}
                  item={this.state.item}
                  items={this.state.items}
                  loadingItems={loadingItems}
                  loadingTable={
                    loadingItems &&
                    this.state.itemDetails.sales_order_details.length > 0
                  }
                  loadingTransactions={loadingTransactions}
                  loadingTable={loadingUpdateSalesOrderDetails}
                  projects={projects}
                  custDisc={this.state.custDisc}
                  taxes={taxes}
                  transaction_status={4}
                />
              </Col>
              <Col span={12}>
                <Col span={10} style={{ marginTop: "15px", textAlign: "left" }}>
                  <strong>
                    <IntlMessages id="purchaseInvoices.totalQty" />
                  </strong>
                  <strong>
                    <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                  </strong>
                </Col>
                <Col span={13} offset={1} style={{ marginTop: "15px" }}>
                  <strong>
                    <NumberFormat value={this.state.total_qty} />
                  </strong>
                </Col>
              </Col>
            </Row>

            <Row>
              <Col span={14} offset={10} style={{ paddingLeft: "15px" }}>
                <Row
                  type="flex"
                  justify="end"
                  style={{ alignItems: "center", marginBottom: "6px" }}
                >
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.exlDiscountAndTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat
                        value={this.state.total_exclude_discount_tax}
                      />
                    </span>
                  </Col>
                </Row>

                <Row
                  type="flex"
                  justify="end"
                  style={{ alignItems: "center", marginBottom: "6px" }}
                >
                  <Col
                    span={10}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.discount" />
                    {this.state.activeCustDisc ? (
                      <Tooltip placement="bottom">
                        <StatusTag
                          className="billed-payment"
                          style={{ marginLeft: "4px" }}
                        >
                          {this.state.activeCustDisc}
                        </StatusTag>
                      </Tooltip>
                    ) : null}
                  </Col>
                  <Col span={4} offset={1} style={{ marginLeft: "5px" }}>
                    <Tooltip
                      placement="bottom"
                      title={this.state.fullDiscMultiplier || ""}
                    >
                      {getFieldDecorator("discount", {
                        initialValue: 0
                      })(
                        <Input
                          size="small"
                          disabled={
                            !permissions.includes("Edit Discount Sales Order")
                          }
                          // readOnly
                          onChange={this.handleChangeCustDisc}
                          style={{
                            marginTop: "3px",
                            borderColor:
                              this.state.total_exclude_discount_tax -
                                this.state.discount_amount <
                              0
                                ? "#f5222d"
                                : "#d9d9d9"
                          }}
                        />
                      )}
                    </Tooltip>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.discount_amount} />
                    </span>
                  </Col>
                </Row>

                <Row
                  type="flex"
                  justify="end"
                  style={{ alignItems: "center", marginBottom: "6px" }}
                >
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.totalDiscount" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.total_discount} />
                    </span>
                  </Col>
                </Row>

                <Row
                  type="flex"
                  justify="end"
                  style={{ alignItems: "center", marginBottom: "6px" }}
                >
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseOrders.totalTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.total_tax} />
                    </span>
                  </Col>
                </Row>

                <Row
                  type="flex"
                  justify="end"
                  style={{ alignItems: "center", marginBottom: "6px" }}
                >
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="total" />
                    </strong>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.total_amount} />
                    </span>
                  </Col>
                </Row>

                {/* <Row type="flex" justify="end" style={{ alignItems: "center",marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.freightCost" />
                  </Col>
                  <Col span={8} offset={1}>
                    {getFieldDecorator("total_freight_cost", {
                      initialValue: 0
                    })(
                      <Input
                        readOnly={true}
                        onClick={this.handleModalFreightCost}
                        addonAfter={
                          <Tooltip
                            placement="bottom"
                            title={
                              <IntlMessages id="purchaseInvoices.setFreightCost" />
                            }
                          >
                            <ButtonAntd
                              style={{
                                width: "100%",
                                height: "30px",
                                border: "0px",
                                backgroundColor: "transparent"
                              }}
                              loading={loadingForeign}
                              disabled={loadingForeign}
                              onClick={this.handleModalFreightCost}
                              icon="plus"
                            />
                          </Tooltip>
                        }
                      />
                    )}
                  </Col>
                </Row> */}

                {/* <Row type="flex" justify="end" style={{ alignItems: "center",marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="purchaseOrders.downPayment" />
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="salesOrders.downPayment.help" />
                        }
                      >
                        
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    </strong>
                  </Col>
                  <Col span={8} offset={1}>
                    {getFieldDecorator("amount", {
                      initialValue: 0
                    })(
                      <Input
                        readOnly={true}
                        onClick={this.handleModalDownPayment}
                        addonAfter={
                          <Tooltip
                            placement="bottom"
                            title={
                              <IntlMessages id="purchaseOrders.setDownPayment" />
                            }
                          >
                            <ButtonAntd
                              style={{
                                width: "100%",
                                height: "30px",
                                border: "0px",
                                backgroundColor: "transparent"
                              }}
                              loading={loadingForeign}
                              disabled={loadingForeign}
                              onClick={this.handleModalDownPayment}
                              icon="plus"
                            />
                          </Tooltip>
                        }
                      />
                    )}
                  </Col>
                </Row> */}
              </Col>
            </Row>
          </TabPane>
          <TabPane
            tab={<IntlMessages id="salesOrders.tab.paymentAndShipment" />}
            key={2}
          >
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.payment" />
                </Divider>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.paymentCategory" />
                      }
                    >
                      {getFieldDecorator("id_payment_category", {
                        // initialValue: data.id_payment_category
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="paymentCategories.button.select" />
                          }
                          loading={loadingForeign}
                          onChange={this.handleChangePaymentCategory}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {paymentCategories.map((data, key) => (
                            <Option key={key} value={data.id_payment_category}>
                              {data.payment_category_code}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.paymentTerm" />}
                    >
                      {getFieldDecorator("id_payment_term", {
                        // initialValue: data.id_payment_term
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          filterOption={true}
                          loading={loadingForeign}
                          notFoundContent={
                            loadingForeign ? (
                              <Spin size="small" />
                            ) : this.state.paymentCategory
                                .id_payment_category ? (
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            ) : (
                              <Empty
                                description={
                                  <IntlMessages id="selectAPaymentCategoryFirst" />
                                }
                              />
                            )
                          }
                          onSelect={this.handleChangePaymentTerm}
                          optionFilterProp="children"
                          placeholder={
                            <IntlMessages id="paymentTerms.button.select" />
                          }
                          showSearch
                          style={{ width: "100%" }}
                        >
                          {getFieldValue("id_payment_category")
                            ? paymentTerms.map((data, key) => {
                                const paymentCategory = paymentCategories.find(
                                  data =>
                                    data.id_payment_category ===
                                    getFieldValue("id_payment_category")
                                );
                                if (
                                  paymentCategory.payment_type === 2 &&
                                  data.is_cash === 0
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                } else if (
                                  paymentCategory.payment_type !== 2 &&
                                  data.is_cash === 1
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                }
                              })
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <InputGroup>
                  <Col span={12} style={{ marginBottom: "-6px" }}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.dueDate" />}
                    >
                      {getFieldDecorator("due_date", {
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY"
                          disabled={true}
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesman" />}
                    >
                      {getFieldDecorator("id_salesman", {
                        // initialValue: data.id_payment_term
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={<IntlMessages id="salesman.select" />}
                          loading={loadingForeign}
                          filterOption={true}
                          optionFilterProp="children"
                        >
                          {employees
                            ? employees.map((data, key) => (
                                <Option key={key} value={data.id_employee}>
                                  {data.employee_code} ({""}
                                  {data.last_name
                                    ? data.first_name + " " + data.last_name
                                    : data.first_name}
                                  {""})
                                </Option>
                              ))
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.inclusiveTax" />}
                    >
                      {getFieldDecorator("inclusive_tax", {
                        initialValue: 0
                      })(
                        <Radio.Group
                          onChange={this.handleInclusiveTax}
                          // value={this.state.inclusive_tax}
                          buttonStyle="solid"
                        >
                          <Radio.Button value={0}>
                            <IntlMessages id="no" />
                          </Radio.Button>
                          <Radio.Button value={1}>
                            <IntlMessages id="yes" />
                          </Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.shipment" />
                </Divider>
                <Row>
                  <Col span={24}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="itemTransfers.courier" />}
                    >
                      {getFieldDecorator("id_courier", {
                        // initialValue: data.id_courier,
                      })(
                        <Select
                          showSearch
                          allowClear
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="itemTransfers.courier" />
                          }
                          loading={loadingForeign}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {couriers.map((data, key) => (
                            <Option key={key} value={data.id_courier}>
                              {`${data.courier_name} (${data.description})`}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12} style={{ paddingRight: "8px" }}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="customers.tab.contact" />}
                    >
                      {getFieldDecorator("contact_person", {
                        // initialValue: data.ship_to
                      })(
                        <AutoComplete
                          style={{ width: "100%" }}
                          dataSource={this.state.customerContactsName}
                          placeholder={<IntlMessages id="contactPerson" />}
                          filterOption={(inputValue, option) =>
                            option.props.children
                              .toUpperCase()
                              .indexOf(inputValue.toUpperCase()) !== -1
                          }
                          onBlur={this.handleCustomerContact}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="phone" />}
                    >
                      {getFieldDecorator(
                        "phone",
                        {}
                      )(
                        <Input
                          style={{ width: "100%" }}
                          placeholder={getLanguage({ id: "phone" })}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>

                <Row>
                  <Col span={12} style={{ paddingRight: "8px" }}>
                    <InputGroup>
                      <Col span={24}>
                        <Form.Item
                          {...formItemLayout}
                          label={
                            <Tooltip
                              placement="top"
                              title={<IntlMessages id="address.select.help" />}
                            >
                              <IntlMessages id="address.select" />
                              <Icon
                                style={{ marginLeft: "5px" }}
                                type="question-circle"
                                theme="filled"
                              />
                            </Tooltip>
                          }
                        >
                          {getFieldDecorator("address_name", {
                            // initialValue: data.id_payment_term
                          })(
                            <Select
                              showSearch
                              style={{ width: "100%" }}
                              onSelect={this.renderAddress}
                              placeholder={<IntlMessages id="address.select" />}
                              filterOption={true}
                              optionFilterProp="children"
                              loading={loadingForeign}
                              notFoundContent={
                                this.state.id_customer ? (
                                  <Empty
                                    description={
                                      <IntlMessages id="customers.noAddress" />
                                    }
                                  />
                                ) : (
                                  <Empty
                                    description={
                                      <IntlMessages id="pleaseSelectCustomerFirst" />
                                    }
                                  />
                                )
                              }
                            >
                              {customerAddresses && customerAddresses.length > 0
                                ? customerAddresses.map((data, key) => (
                                    <Option
                                      key={key}
                                      value={data.id_customer_address}
                                    >
                                      {data.address} ({""}
                                      {data.customer_address_name}
                                      {""})
                                    </Option>
                                  ))
                                : null}
                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                    </InputGroup>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      style={{ paddingBottom: 0 }}
                      label={<IntlMessages id="salesOrders.shipDate" />}
                    >
                      {getFieldDecorator("ship_date", {
                        // initialValue: moment()
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          allowClear={true}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={24}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="salesOrders.shipTo" />}
                    >
                      {getFieldDecorator("ship_to", {
                        // initialValue: data.ship_to
                      })(<TextArea autosize={{ minRows: 4 }} />)}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            disabled
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
            key={3}
          />
        </Tabs>

        <Form.Item {...tailFormItemLayout}>
          <Button
            disabled={
              !permissions.includes("Create Sales Order") ? true : false
            }
            type="primary"
            htmlType="submit"
            style={{ marginTop: "24px" }}
            loading={loading || loadingSubmit}
            onClick={() => removeSaveData("so")}
            disabled={
              this.state.total_exclude_discount_tax -
                this.state.total_discount <
              0
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          <Row>
            <Col span={24}>
              {this.state.total_exclude_discount_tax -
                this.state.total_discount <
                0 || this.state.err_discount ? (
                <IntlMessages id="discountExceed">
                  {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
                </IntlMessages>
              ) : null}
            </Col>
            <Col span={24}>
              {this.state.err_item ? (
                <IntlMessages id="itemDataEmpty">
                  {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
                </IntlMessages>
              ) : null}
            </Col>
          </Row>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedSalesOrderCreateForm = Form.create({
  name: "create_sales_order"
})(SalesOrderCreateForm);

const mapStateToProps = state => {
  return { user: state.auth.user };
};

export default compose(
  connect(mapStateToProps, {
    getCustomerContacts,
    getItemsForSO,
    getUpdateSODetails
  })
)(WrappedSalesOrderCreateForm);
